@import url("https://fonts.googleapis.com/css2?family=Raleway:ital,wght@1,800&family=Source+Code+Pro:wght@600&display=swap");
body {
  margin: 0;
  font-family: "Raleway", "Source Code Pro", "monospace", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(23, 20, 20);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.heading{
  font-size: 3rem;
  text-align: center;
  margin-top: 25px;
}
.cards{
  display:inline-block;
  margin:20px auto;
  
  
}
.card{
  margin:20px 70px;
  max-width:550px;
  min-height: 200px;
  border:1px solid rgb(43, 38, 38);
  background-color: rgb(7, 7, 7);
  display:flex;
  flex-direction: column;
  align-items: center;
  padding:20px;
  border-radius: 5px;
  transition: ease 1s;
}
.card-img{
  border-radius: 5px;
  width:275px;
  height:300px;
}
.card-rank{
  font-size: 1.5rem;
  margin:5px auto;
}
.card-title{
  font-size: 2rem;
  margin:5px auto;
}
.about-card{
  margin:5px auto;
}
.text-light{
  color:white;
}
.margin-top{
  margin-top: 20px;
}
button{
  cursor: pointer;
}
.card:hover{
  transform: scale(110%);
}
.btn{
  transition: ease 0.5s;
  background-color: crimson;
  z-index: 1;
  position: relative;
}
.btn:hover {
  font-size: 1.2rem;
  color: rgb(46, 42, 42);
}
.btn:before {
  transition: 0.5s all ease;
  position: absolute;
  top: 0;
  left: 50%;
  right: 50%;
  bottom: 0;
  opacity: 0;
  content: "";
  background-color: rgb(115, 255, 0);
}
.btn:hover:before {
  transition: 0.5s all ease;
  left: 0;
  right: 0;
  opacity: 1;
  z-index: -1;
}
